.vocal-diagnosis,
.your-reports {
  align-self: stretch;
  position: relative;
}
.your-reports {
  z-index: 0;
}
.vocal-diagnosis {
  text-align: left;
}
.your-diagnosis-is {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.autism-negative {
  align-self: stretch;
  position: relative;
  color: var(--color-limegreen);
}
.your-diagnosis-is-parent {
  flex: 1;
  border-radius: 10px;
  background-color: var(--color-white);
  height: 143px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 17px 10px;
  box-sizing: border-box;
  gap: 10px;
}
.frame-wrapper {
  align-self: stretch;
  height: 148px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  font-size: var(--font-size-xs);
}
.learn-more {
  width: 69px;
  position: relative;
  font-weight: 600;
  display: inline-block;
  flex-shrink: 0;
}
.learn-more-wrapper {
  text-decoration: none;
  width: 89px;
  border-radius: var(--br-xl);
  background-color: var(--color-lightskyblue);
  height: 26px;
  flex-direction: row;
  padding: 3px var(--padding-xl);
  font-size: 10px;
  color: var(--color-white);
}
.final-reports-inner,
.learn-more-wrapper,
.vocal-diagnosis-parent {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.vocal-diagnosis-parent {
  width: 450px;
  border-radius: var(--br-xl);
  background-color: #e2eef5;
  height: 284px;
  flex-direction: column;
  padding: 27px var(--padding-xl);
  gap: var(--gap-mid);
}
.final-reports-inner {
  width: 851px;
  height: 529px;
  flex-direction: row;
  padding: 0 200px;
  z-index: 1;
  text-align: center;
  font-size: 20px;
  color: var(--color-steelblue);
}
.final-reports-child {
  cursor: pointer;
  z-index: 2;
}
.screenshot-2023-12-20-at-9303 {
  width: 67px;
  position: relative;
  height: 68px;
  object-fit: cover;
}
.frame-child1 {
  width: 90px;
  position: relative;
  border-top: 7px solid var(--color-lightskyblue);
  box-sizing: border-box;
  height: 7px;
}
.screenshot-2023-12-20-at-930-group {
  width: 102px;
  margin: 0 !important;
  position: absolute;
  top: 42px;
  left: 60px;
  height: 99px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mid);
  z-index: 3;
}
.final-reports {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 1144px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 150px var(--padding-81xl) var(--padding-81xl);
  box-sizing: border-box;
  gap: 30px;
  text-align: left;
  font-size: 30px;
  color: var(--color-darkslategray);
  font-family: var(--font-noto-sans);
}
